<template>
    <div id="app">
        <header>
            <div :class="'banner ' + contentWidthClass">
                <div class="banner-label">
                    <div class="banner-name">{{ config.blogName }}</div>
                    <div class="banner-tips">{{ config.tips }}</div>
                </div>
                <div class="banner-nav">
                    <template v-for="(v, i) in category">
                        <span v-if="v === currentCate" class="selected" v-on:click="goTo(v)">{{ v }}</span>
                        <span v-else v-on:click="goTo(v)">{{ v }}</span>
                    </template>
                </div>
            </div>
        </header>
        <div :class="'content ' + contentWidthClass">
            <router-view/>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { mapActions, mapGetters } from 'Vuex'

export default {
    name: 'app',
    data () {
        return {
            cate: [],
            contentWidthClass: 'content-width-normal',
            screenWidth: document.body.clientWidth
        }
    },
    methods: {
        ...mapActions([
            'setConfig',
            'setCategory',
            'setCurrentCate'
        ]),
        goTo (cate) {
            this.$router.push({
                name: 'list',
                params: {category: cate}
            })
        },
        setScreenWidth () {
            window.screenWidth = document.body.clientWidth
            this.screenWidth = window.screenWidth

            if (this.screenWidth > 1024) {
                this.contentWidthClass = 'content-width-normal'
            } else {
                this.contentWidthClass = 'content-width-small'
            }
        }
    },
    computed: {
        ...mapGetters([
            'config',
            'currentCate',
            'category'
        ])
    },
    mounted: function () {
        if (!this.config.blogName) {
            axios.get('/static/Config.json')
                .then((response) => {
                    this.setConfig(response.data)
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        if (this.category.length === 0) {
            axios.get('/static/articles/category.json')
                .then((response) => {
                    this.setCategory(response.data)
                })
                .catch((error) => {
                    console.log(error)
                })
        }

        // 检测屏幕大小变化
        const that = this
        window.onresize = () => {
            return (() => {
                that.setScreenWidth()
            })()
        }
    },
    watch: {
        screenWidth (val) {
            if (!this.timer) {
                this.screenWidth = val
                this.timer = true
                let that = this
                setTimeout(function () {
                    // console.log(that.screenWidth)
                    that.setScreenWidth()
                    that.timer = false
                }, 400)
            }
        }
    }
}
</script>

<style>
body {
    margin: 0;
    background-color: #f7f7f7;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* text-align: center; */
    color: #2c3e50;
    /*margin-top: 60px;*/
}

header {
    box-sizing: border-box;
    /*padding: 90px 15px;*/
    width: 100%;
    height: 150px;
    color: #fff;
    /*text-align: center;*/
    background-color: #1abc9c;
    background-image: linear-gradient(120deg,#155799,#159957);
    border-color: #16a085;
    padding-top: 20px;
    padding-bottom: 20px;
}

.content-width-normal {
    width: 70%;
    max-width: 1440px;
}

.content-width-small {
    width: 90%;
}

.selected {
    border-bottom: 2px solid red;
}

.banner {
    margin: 0 auto;
    height: 100%;
    line-height: 50px;
    font-size: 18px;
    font-weight: bold;
}

.banner-nav {
    width: 100%;
    text-align: right;
    margin-top: 20px;
}

.banner-nav span {
    margin-left: 10px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    height: 20px;
    line-height: 20px;
}

.banner-nav span:hover {
    color: #1abc9c;
}

.banner-label {
    width: 100%;
}

.banner-name {
    font-size: 40px;
    line-height: 40px;
    letter-spacing: 2px;
    height: 50px;
}

.banner-tips {
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 1px;
}

.content {
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 50px;
}

</style>



