import * as types from './mutation-types'

const mutations = {
    [types.SET_CONFIG] (state, value) {
        state.config = value
    },
    [types.SET_CURRENT_CATE] (state, value) {
        state.currentCate = value
    },
    [types.SET_CATEGORY] (state, value) {
        state.category = value
    },
    [types.SET_ARTICLE_LIST] (state, value) {
        state.list = value
    },
    [types.SET_ARTICLE_CONTENT] (state, value) {
        state.articleContent = value
    }
}

export default mutations



