<template>
    <div class="list">
        <template v-for="(v, i) in mdList">
            <div :class="'list-content left-border-' + v.notes.dateS[1]" v-on:click="goTo(v)">
                <div class="list-date">
                    <div class="list-date-day">{{ v.notes.dateS[2] }}</div>
                    <div class="list-date-year">{{ v.notes.dateS[0] + '/' + v.notes.dateS[1] }}</div>
                </div>
                <div class="list-title">
                    <div class="list-title-text">{{ v.name }}</div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
import axios from 'axios'
import { mapActions, mapGetters } from 'Vuex'

export default {
    name: 'HelloWorld',
    data () {
        return {
            mdList: []
        }
    },
    methods: {
        ...mapActions([
            'setCurrentCate',
            'setList'
        ]),
        goTo (v) {
            this.$router.push({
                name: 'article',
                params: {
                    category: v.cate,
                    name: v.name
                }
            })
        },
        loadList () {
            if (!this.category[0]) {
                return
            }

            let cateName = this.category[0]

            if (this.$route.params && this.$route.params.category) {
                cateName = this.$route.params.category
            }

            this.setCurrentCate(cateName)

            if (this.list[cateName]) {
                this.mdList = this.list[cateName]
            } else {
                axios.get('/static/articles/' + cateName + '.json')
                    .then((response) => {
                        this.list[cateName] = response.data
                        this.mdList = this.list[cateName]

                        this.setList(this.list)
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            }
        }
    },
    computed: {
        ...mapGetters([
            'currentCate',
            'list',
            'category'
        ])
    },
    mounted: function () {
        this.loadList()
    },
    watch: {
        $route: function () {
            this.loadList()
        },
        category: function () {
            this.loadList()
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list-content {
    margin-top: 30px;
    background-color: #fff;
    box-shadow: 2px 2px 5px #e6e3e3;
    border: 1px solid #f0f0f0;
    height: 60px;
    width: 100%;
    position: relative;
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
}

.list-content:hover {
    background-color: #F0F0F0;
}

.list-date {
    display: inline-block;
    width: 20%;
    min-width: 60px;
    max-width: 80px;
    text-align: center;
    position: relative;
    height: 100%;
    border-right: 1px solid #b9b7b7;
    line-height: 100%;
    vertical-align: middle;
}

.list-date-day {
    height: 30px;
    line-height: 30px;
    font-size: 20px;
    font-weight: bold;
}

.list-date-year {
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    color: #888;
}

.left-border-1 {
    border-left: 4px solid #1dade2;
}

.left-border-2 {
    border-left: 4px solid #17a552;
}

.left-border-3 {
    border-left: 4px solid #8a7838;
}

.left-border-4 {
    border-left: 4px solid #ec6236;
}

.left-border-5 {
    border-left: 4px solid #a8a8a6;
}

.left-border-6 {
    border-left: 4px solid #71619b;
}

.left-border-7 {
    border-left: 4px solid #97c9dd;
}

.left-border-8 {
    border-left: 4px solid #ebe642;
}

.left-border-9 {
    border-left: 4px solid #935a44;
}

.left-border-10 {
    border-left: 4px solid #f37f23;
}

.left-border-11 {
    border-left: 4px solid #90ad84;
}

.left-border-12 {
    border-left: 4px solid #c1558b;
}

.list-title {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    width: 75%;
    overflow: hidden;
}

.list-title-text {
    font-size: 20px;
    height: 60px;
    line-height: 60px;
    margin-left: 10px;
    font-weight: bold;
}
</style>



