import Vue from 'vue'
import Router from 'vue-router'
import List from '@/components/List'
import Article from '@/components/Article'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: List
        },
        {
            path: '/list/:category',
            name: 'list',
            component: List
        },
        {
            path: '/article/:category/:name',
            name: 'article',
            component: Article
        }
    ]
})



