<template>
    <div class="white-bg">
        <div class="article-top">
            <div class="article-name">{{ article.name }}</div>
            <div class="article-info">By: <span> Abin </span> At: <span>{{ article.notes.date }}</span></div>
        </div>
        <mavon-editor :ishljs="true" code_style="monokai-sublime" :toolbarsFlag="false" :subfield="false" default_open="preview" v-model="article.content"></mavon-editor>
    </div>
</template>

<script>
import axios from 'axios'
import { mapActions, mapGetters } from 'Vuex'

export default {
    name: 'Article',
    data () {
        return {
            article: {
                name: '',
                notes: {},
                content: ''
            }
        }
    },
    methods: {
        ...mapActions([
            'setCurrentCate',
            'setArticleContent',
            'setList'
        ]),
        loadContent () {
            let category = this.$route.params.category
            let name = this.$route.params.name

            let str = encodeURIComponent(category + '.' + name)
            let key = btoa(str)
            if (this.articleContent[key]) {
                this.article = this.articleContent[key]
            } else {
                axios.get('/static/articles/' + category + '/' + name + '.md')
                    .then((response) => {
                        this.article.content = response.data

                        let info = this.getArticleInfo(category, name)
                        this.article.name = name
                        this.article.notes = info.notes

                        this.articleContent[key] = this.article
                        this.setArticleContent(this.articleContent)
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            }
        },
        getArticleInfo (category, name) {
            let info = {}
            this.list[category].forEach((v) => {
                if (v.name === name) {
                    info = v
                }
            })
            return info
        },
        refresh () {
            let category = this.$route.params.category

            this.setCurrentCate(category)

            if (category && this.list[category]) {
                this.loadContent()
            } else {
                axios.get('/static/articles/' + category + '.json')
                    .then((response) => {
                        console.log(response)
                        this.list[category] = response.data
                        this.setList(this.list)

                        this.loadContent()
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            }
        }
    },
    computed: {
        ...mapGetters([
            'articleContent',
            'list'
        ])
    },
    mounted: function () {
        this.refresh()
    },
    watch: {
        $route: function () {
            this.refresh()
        }
    }
}
</script>

<style>
.white-bg {
    background-color: #fbfbfb;
    box-shadow: 0 0px 3px rgba(0,0,0,0.157), 0 0px 3px rgba(0,0,0,0.227);
}

#editor {
    margin: auto;
    width: 80%;
    height: 580px;
}

.article-top {
    padding: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #eaecef;
    text-align: center;
    position: relative;
}

.article-name {
    font-size: 25px;
    font-weight: bold;
    line-height: 40px;
    /*height: 40px;*/
    color: #24292e;
}

.article-info {
    color: #d3cacd;
}

.article-info span {
    color: #a1a1a1;
    margin-right: 20px;
}

.v-note-wrapper .v-note-panel {
    box-shadow: none !important;
}
</style>



