import * as types from './mutation-types'

export const setConfig = ({commit}, value) => {
    commit(types.SET_CONFIG, value)
}

export const setCurrentCate = ({commit}, value) => {
    commit(types.SET_CURRENT_CATE, value)
}

export const setCategory = ({commit}, value) => {
    commit(types.SET_CATEGORY, value)
}

export const setList = ({commit}, value) => {
    commit(types.SET_ARTICLE_LIST, value)
}

export const setArticleContent = ({commit}, value) => {
    commit(types.SET_ARTICLE_CONTENT, value)
}



